@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=VT323&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

body {
  background-image: url("./images/background.jpg");
  background-size: cover;
}

h1,
h4,
p,
a {
  color: darkgreen;
  font-weight: 500;
  text-decoration: none;
}

ul {
  list-style-type: none;
}
